<template>
  <div>
    <Scroll :data="topList" class="rank" ref="scroll">
      <div class="rankWrapper">
        <ul>
          <li v-for="item in topList" @click="selectItem(item)">
            <div class="rankDetail">
              <div class="icon">
                <img v-lazy="item.picUrl" alt="" height="100px" width="100px">
              </div>
              <div class="text">
                <ul>
                  <li v-for="(song,index) in item.songList">
                    <div class="containerText">
                      <span class="name">{{index + 1}}</span>
                      <div class="title2">
                    <span class="title">{{song.songname}}</span>
                    <span class="name">-{{song.singername}}</span>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="arrow">
                <i></i>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="loading-container" v-show="!topList.length">
        <loading></loading>
      </div>
    </Scroll>
  <router-view></router-view>
  </div>
</template>

<script>
  import {getTop} from "../../api/rank";
  import {ERR_OK} from "../../api/config";
  import Scroll from '../../base/scroll'
  import loading from '../../base/loading'
  import {playlistMixin} from '../../common/js/mixin'
  import {mapMutations} from 'vuex'

  export default {
        name: "rank",
    mixins:[playlistMixin],
    components:{
          Scroll,
      loading
    },
    data(){
      return {
        topList:[]
      }
    },
      created(){
          this._getTop()
      },
      methods:{
          _getTop(){
            getTop().then((res)=>{
              if(res.code===ERR_OK){
                this.topList = res.data.topList
              }
            })
          },
        ...mapMutations({
          setTopList: 'SET_TOP_LIST'
        }),
        handlePlayList(playList){
          const bottom =playList.length>0 ? '66px':''
          this.$refs.scroll.$el.style.bottom = bottom
          this.$refs.scroll.refresh()
        },
        selectItem(item){
            this.$router.push({
              path:`/rank/${item.id}`
            });
            this.setTopList(item)
        }
      }
    }
</script>

<style scoped lang="stylus">
      .rank{
        position:absolute
        top:88px
        bottom:0
        width: 100%
        overflow :hidden
        background: #f4f4f4
        .rankWrapper{
          position:relative
          top:0
          overflow: hidden
          width: 100%
          .rankDetail{
            display:flex
            align-items :center
            height:100px
            margin:10px
            background: #FFFFFF
            .icon{
              width:100px
              height:100px
            }
            .text{
              flex:1
              margin:20px 10px
              display:flex
              align-items :center
              overflow :hidden
              .containerText{
                height:25px
                padding:5px
                display :flex
                font-size :14px
                .title2{
                  flex:1
                  overflow :hidden
                  white-space :nowrap
                  text-overflow:ellipsis
                  width:210px
                }
                .title{
                  color:#000
                  overflow :hidden
                  white-space :nowrap
                  text-overflow:ellipsis
                  margin-left :5px

                }
                .name{
                  color:rgba(0,0,0,.5)
                  overflow :hidden
                  white-space :nowrap
                  text-overflow:ellipsis

                }
              }
            }
            .arrow{
              width:8px
              height:100%
              display :flex
              align-items :center
              justify-content :center
              background-color :rgba(0,0,0,0.1)
              border-radius :1px
              i{
                border:5px solid transparent
                border-left-color :rgba(0,0,0,0.2)
                height:0
                width:0
                margin-left :4px
              }
            }
          }
        }
      }

      .loading-container{
        position: absolute
        top:50%
        width :100%
        transition :translateY(-50%)
      }
</style>



